<template>
	<view style="background-color: #F1F1F1;position: fixed;width: 100%;height: 100%;">
		
		<view class="funBody">
			
			<view class="funItem" @click="toUploadPage">
				<image class="itemIcon" src="../../static/上传.png" mode=""></image>
				<text class="itemText">台区上传</text>
			</view>
			<view class="funItem" @click="toViewCourts">
				<image class="itemIcon" src="../../static/检查.png" mode=""></image>
				<text class="itemText">台区查看</text>
			</view>
			
			<view class="funItem" @click="toDeviceMan">
				<image class="itemIcon" src="../../static/管理.png" mode=""></image>
				<text class="itemText">设备管理</text>
			</view>
			
			<view class="funItem" @click="toCheckList">
				<image class="itemIcon" src="../../static/巡检.png" mode=""></image>
				<text class="itemText">台区巡检</text>
			</view>
			<view class="funItem" @click="todeviceStatistics">
				<image class="itemIcon" src="../../static/统计.png" mode=""></image>
				<text class="itemText">设备统计</text>
			</view>
			<view class="funItem"@click="toUser" v-show="role == '2'">
				<image class="itemIcon"  src="../../static/用户管理.png" mode=""></image>
				<text class="itemText">用户管理</text>
			</view>
			<view class="funItem"@click="toNotice" >
				<image class="itemIcon"  src="../../static/通知.png" mode=""></image>
				<text class="itemText">通知公告</text>
			</view>
			
		</view>
		
		
		
		
		
		
		
		
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				srcUrl:"../../static/logo.png",
				role:'',
			}
		},
		methods: {
			//台区上传
			toUploadPage:function(){
				let that = this;
				console.log("权限级别：",that.role);
				if(that.role === ''){
					uni.showModal({
						title:"提示",
						content:"请先登录"
					})
				}else if(that.role == "2"){
					uni.navigateTo({
						url:"../uploadPic/uploadPic"
					})
				}else{
					uni.showModal({
						title:"提示",
						content:"权限不足"
					})
				}
			},
			//台区查看(允许所有人)
			toViewCourts:function(){
				let that = this;
				if(that.role=== ""){
					uni.showModal({
						title:"提示",
						content:"请先登录！"
					})
				}else{
					uni.navigateTo({
						url:"../viewCourts/viewCourts"
					})
				}
			},
			//设备管理
			toDeviceMan:function(){
				let that = this;
				console.log("权限级别：",that.role);
				if(that.role=== ""){
					uni.showModal({
						title:"提示",
						content:"请先登录"
					})
				}else if(that.role != "1"){
					uni.navigateTo({
						url:"../deviceMan/deviceMan"
					})
				}else{
					uni.showModal({
						title:"提示",
						content:"权限不足，请联系管理员"
					})
				}
			},
			//设备统计(允许所有人)
			todeviceStatistics:function(){
				let that = this;
				if(that.role=== ""){
					uni.showModal({
						title:"提示",
						content:"请先登录"
					})
				}else{
					uni.navigateTo({
						url:"../deviceStatistics/deviceStatistics"
					})
				}
			},
			//台区巡检（允许所有人）
			toCheckList:function(){
				let that = this;
				console.log("权限级别：",that.role);
				if(that.role=== ""){
					uni.showModal({
						title:"提示",
						content:"请先登录"
					})
				}else{
					uni.navigateTo({
						url:"../checkList/checkList"
					})
				}
			},
			//用户管理（管理员）
			toUser:function(){
				uni.navigateTo({
					url:"../userManagement/userManagement"
				})
			},
			//通知
			toNotice:function(){
				let that = this;
				if(that.role=== ""){
					uni.showModal({
						title:"提示",
						content:"请先登录"
					})
				}else{
					uni.navigateTo({
						url:"../noticeList/noticeList"
					})
				}
			},
		},
		onShow() {
			console.log("该页面也显示")
			let that = this;
			uni.getStorage({
				key: 'role',
				success (res) {
					that.role = res.data
				},
				fail(res) {
					console.log(res)
					console.log("用户未登录")
				}
			})
		}
	}
</script>

<style>

.funBody{
	width: 95%;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}
.funItem{
	width: 350rpx;
	height: 155rpx;
	margin-top: 20rpx;
	border-radius: 20rpx;
	background-color: #FFFFFF;
	position: relative;
}
.itemIcon{
	width: 140rpx;
	height: 140rpx;
	margin: 8rpx;
	float: left;
}
.itemText{
	position: absolute;
	font-weight: 600;
	font-size: 40rpx;
	bottom: 28rpx;
	right: 35rpx;
}
swiper-item{
	background-color: #fff;
}
</style>
